// import 'antd/lib/message/style/index.css';
import './markdownEditor.css'

import 'codemirror/theme/yonce.css'
import 'codemirror/lib/codemirror.css'
import 'codemirror/addon/fold/foldgutter.css'; 


function MarkdownEditor(props) {
  const container = useRef(null);
  const { value, onChange } = props;
  let [ init, setInit ] = useState(false)

  useEffect(() => {
    // if(typeof window!==`undefined`&&typeof navigator!==`undefined`){
    //   let {editor} = require('monaco-editor')
    //   const _editor =  editor.create(container.current, {
    //         value: value,
    //         language: "markdown",
    //         theme:"vs-dark",
    //         lineNumbers: 'on',
    //         roundedSelection: false,
    //         scrollBeyondLastLine: false,
    //         readOnly: false,
    //         automaticLayout: true,				
    //         glyphMargin: true,
    //         lightbulb: {
    //           enabled: true
    //         }
    //       });
    //   const _model = _editor.getModel();
    
      
    //   _model.onDidChangeContent(e=>{
    //     let x = _model.getValue()
    //     onChange(x)
    //   })
  // }
  if(!init){
    require('codemirror/lib/codemirror.js')
    
    require('codemirror/mode/markdown/markdown')
    require('codemirror/addon/selection/active-line');
    require('codemirror/addon/fold/foldcode.js')
    require('codemirror/addon/fold/foldgutter.js') 
    require('codemirror/addon/fold/brace-fold.js')  
    require('codemirror/addon/fold/comment-fold.js')
    let CodeMirror = require('codemirror')(container.current,{
      mode:"markdown",
      theme:"yonce",
      value:value,
      lineNumbers:true
    });
    CodeMirror.setOption("value", value);
    CodeMirror.setOption("save",()=>{
      
    })

    async function doChange(x){
      await onChange(x)
    }
    CodeMirror.on('keyup',()=>{
      // console.log(CodeMirror.getValue())
      doChange(CodeMirror.getValue())
      
    })
    setInit(true)
  }
  }, [value, onChange,init])
  return (
    <div className="markdownEditor" ref={container} />
  );
}

MarkdownEditor.defaultProps = {
  value: '',
  onChange:() => { }
};

export default MarkdownEditor;