'use strict';

var mermaid = require('mermaid');

var mermaidParser = require('./mermaid-parser.js');

module.exports = function mermaid_plugin(md, options) {
  md.mermaid = mermaid;
  options = options || {};
  mermaidParser.functions.initialize(options);
  var defaultRenderer = md.renderer.rules.fence.bind(md.renderer.rules);

  md.renderer.rules.fence = function (tokens, idx, options, env, slf) {
    var token = tokens[idx];
    var code = token.content.trim();
    var info = token.info ? md.utils.unescapeAll(token.info).trim() : '';
    var langName = '';

    if (info) {
      langName = info.split(/\s+/g)[0];
    }

    if (langName === 'mermaid') {
      return mermaidParser.functions.getMarkup(code);
    }

    return defaultRenderer(tokens, idx, options, env, slf);
  };
};